import { css } from "styled-components"

export const outlinedRedButton = css`
  color: var(--hight-contrast);
  border: 1px solid var(--tint-background-red);
  background-color: rgba(var(--red-rgb-inner), 0.1);
  :hover {
    background-color: rgba(var(--red-rgb-inner), 0.2);
  }
  svg {
    fill: var(--hight-contrast) !important;
  }
`

