import React from "react"
import * as S from "./styles"

export default function Button(props) {
  const tagName = props.href ? "a" : props.as
  const isIconButton = React.Children.count(props.children) === 0 ? true : false
  const inputValue = props.value ?? "Input button"
  const type = props.type ?? "button"

  switch (tagName) {
    case "input":
      return (
        <S.Button
          // default props
          id={props.id}
          className={props.className}
          style={props.style}
          onClick={props.onClick}
          // custom props
          as="div"
          size={props.size}
          variant={props.variant}
          transparent={props.transparent}
          disabled={props.disabled}
          block={props.block}
        >
          {props.leftIcon}
          <input type={type} value={inputValue} />
          {props.rightIcon}
        </S.Button>
      )

    default:
      return (
        <S.Button
          // default props
          id={props.id}
          className={props.className}
          style={props.style}
          onClick={props.onClick}
          type={props.type}
          aria-label={props["aria-label"]}
          // custom props
          as={tagName}
          size={props.size}
          variant={props.variant}
          transparent={props.transparent}
          disabled={props.disabled}
          block={props.block}
          isIconButton={isIconButton}
          // -> a
          href={props.href}
          target={props.target}
          role={props.role}
        >
          {props.leftIcon}
          {props.children}
          {props.rightIcon}
        </S.Button>
      )
  }
}
