import { styleVariables } from "projectSettings"
import styled, { css } from "styled-components"
import {
  filledRedButton,
  filledOrangeButton,
  filledYellowButton,
  filledGreenButton,
  filledTealButton,
  filledBlueButton,
  filledIndigoButton,
  filledPurpleButton,
  filledPinkButton,
  filledGrayButton,
} from "./variants/filled"
import {
  outlinedRedButton,
  outlinedOrangeButton,
  outlinedYellowButton,
  outlinedGreenButton,
  outlinedTealButton,
  outlinedBlueButton,
  outlinedIndigoButton,
  outlinedPurpleButton,
  outlinedPinkButton,
  outlinedGrayButton,
} from "./variants/outlined"
import {
  plainRedButton,
  plainOrangeButton,
  plainYellowButton,
  plainGreenButton,
  plainTealButton,
  plainBlueButton,
  plainIndigoButton,
  plainPurpleButton,
  plainPinkButton,
  plainGrayButton,
} from "./variants/plain"
import { largeButton, mediumButton, smallButton } from "./sizes"

const transparentButton = css`
  background-color: transparent !important;
`

const disabledButton = css`
  opacity: 0.6;
  pointer-events: none;
`

const blockButton = css`
  justify-content: center;
  width: 100%;
`

export const Button = styled.button`
  display: flex;
  gap: 8px;
  position: relative;
  width: max-content;
  line-height: 100%;
  align-items: center;
  font-weight: normal;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  text-align: center;
  border: none;
  border-radius: ${styleVariables.borderRadius.primary};
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out,
    opacity 0.15s ease-in-out, filter 0.15s ease-in-out;
  :focus {
    outline: none;
  }
  svg {
    transition: fill 0.15s ease-in-out;
    opacity: 0.5;
    fill: var(--hight-contrast) !important;
  }
  input {
    color: inherit;
    background-color: transparent;
    border: none;
    padding: 0;
    pointer-events: none;
  }

  // Variants
  // -> filled
  ${props => (props.variant === "red" ? filledRedButton : null)}
  ${props => (props.variant === "orange" ? filledOrangeButton : null)}
  ${props => (props.variant === "yellow" ? filledYellowButton : null)}
  ${props => (props.variant === "green" ? filledGreenButton : null)}
  ${props => (props.variant === "teal" ? filledTealButton : null)}
  ${props =>
    !props.variant || props.variant === "blue" ? filledBlueButton : null}
  ${props => (props.variant === "indigo" ? filledIndigoButton : null)}
  ${props => (props.variant === "purple" ? filledPurpleButton : null)}
  ${props => (props.variant === "pink" ? filledPinkButton : null)}
  ${props => (props.variant === "gray" ? filledGrayButton : null)}
  // -> outlined
  ${props => (props.variant === "outlined-red" ? outlinedRedButton : null)}
  ${props =>
    props.variant === "outlined-orange" ? outlinedOrangeButton : null}
  ${props =>
    props.variant === "outlined-yellow" ? outlinedYellowButton : null}
  ${props => (props.variant === "outlined-green" ? outlinedGreenButton : null)}
  ${props => (props.variant === "outlined-teal" ? outlinedTealButton : null)}
  ${props =>
    props.variant === "outlined" || props.variant === "outlined-blue"
      ? outlinedBlueButton
      : null}
  ${props =>
    props.variant === "outlined-indigo" ? outlinedIndigoButton : null}
  ${props =>
    props.variant === "outlined-purple" ? outlinedPurpleButton : null}
  ${props => (props.variant === "outlined-pink" ? outlinedPinkButton : null)}
  ${props => (props.variant === "outlined-gray" ? outlinedGrayButton : null)}
  // -> plain
  ${props => (props.variant === "plain-red" ? plainRedButton : null)}
  ${props => (props.variant === "plain-orange" ? plainOrangeButton : null)}
  ${props => (props.variant === "plain-yellow" ? plainYellowButton : null)}
  ${props => (props.variant === "plain-green" ? plainGreenButton : null)}
  ${props => (props.variant === "plain-teal" ? plainTealButton : null)}
  ${props =>
    props.variant === "plain" || props.variant === "plain-blue"
      ? plainBlueButton
      : null}
  ${props => (props.variant === "plain-indigo" ? plainIndigoButton : null)}
  ${props => (props.variant === "plain-purple" ? plainPurpleButton : null)}
  ${props => (props.variant === "plain-pink" ? plainPinkButton : null)}
  ${props => (props.variant === "plain-gray" ? plainGrayButton : null)}

  // Size
  ${props => (!props.size || props.size === "md" ? mediumButton : null)}
  ${props => (props.size && props.size === "lg" ? largeButton : null)}
  ${props => (props.size && props.size === "sm" ? smallButton : null)}

  // Transparent
  ${props => (props.transparent ? transparentButton : null)}

  // Disabled
  ${props => (props.disabled ? disabledButton : null)}

  // Block (width 100%)
  ${props => (props.block ? blockButton : null)}
`
