import { css } from "styled-components"

export const largeButton = css`
  height: 48px;
  min-width: 48px;
  font-size: 17px;
  padding: 8px 16px;
  padding: ${props => (props.isIconButton === true ? "12px" : "8px 16px")};
`
export const mediumButton = css`
  height: 40px;
  min-width: 40px;
  font-size: 15px;
  padding: ${props => (props.isIconButton === true ? "8px" : "8px 16px")};
`
export const smallButton = css`
  height: 32px;
  min-width: 32px;
  font-size: 12px;
  padding: ${props => (props.isIconButton === true ? "8px" : "8px 12px")};
`
